.row {
    width: 100%;
    border-bottom: 1px solid #333;
    font-size: 16px;
    display: flex;
    justify-content: space-between;
    padding: 5px;
}

.col {
    width: 50%
}

.col2 {
    width: 50%;
}

.link {
    text-decoration: none;
    color: #333;
    display: flex;
}



.btns {
    display: flex;
    justify-content: space-between;
}

.btn {
    padding: 5px;
}

.update {
    width: 40px;
    height: 40px;
    border: 1px solid yellow;
    background-color: rgba(255, 251, 0, 0.3);
    text-decoration: none;
    color: #333;
}

.update:hover {
    background-color: yellow;
    cursor: pointer;
}